@import './utils/mixins';
@import './utils/variables';

.outletWrapper {
  min-height: calc(100vh - 257px);
  padding-bottom: 56px;
  padding-top: 72px;

  @include on-tablet {
    min-height: calc(100vh - 96px);
    padding-bottom: 64px;
  }

  @include on-desktop {
    min-height: calc(100vh - 96px);
    padding-bottom: 80px;
    padding-top: 88px;
  }
}
